@import "../../global.scss";

.menu {
  display: flex;
  margin: 0;
  background-color: $primarycolor;
  width: 100%;
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  position: fixed;
  top: -100vh;
  transition: all 0.8s ease;

  &.active {
    top: 0;
  }

  .menuBtn {
    width: 25px;
    height: 20px;
    display: flex;
    align-items: flex-end;
    flex-direction: column;
    justify-content: space-between;
    cursor: pointer;
    position: relative;
    top: -0.5rem;
    right: -11rem;

    span {
      width: 100%;
      height: 2px;
      color: #fff;
      transform-origin: left;
      transition: all 0.5s ease;
    }
  }

  .active {
    span {
      &:first-child {
        width: 100%;
        background-color: #fff;
        transform: rotate(45deg);
      }

      &:nth-child(2) {
        opacity: 0;
      }

      &:last-child {
        width: 100%;
        background-color: #fff;
        transform: rotate(-45deg);
      }
    }
  }
  ul {
    padding: 0;

    .social {
      padding: 0 0 10px 0;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 3.2rem;

      span {
        margin-top: 10px;
        padding: 10px;

        a {
          color: #fff;
          transition: all 0.2s ease;
        }

        a:hover {
          color: $fontcolor;
        }
      }
    }
  }
}
