@import "../../global.scss";

.menuLink {
  margin: 0;
  width: 100%;
  text-align: center;
  margin-bottom: 1.5rem;
  border-bottom: 0.5px solid #fff;

  a {
    font-size: 1.7rem;
    font-family: "Baskervville", "serif";
    font-weight: 300;
    text-transform: capitalize;
    color: #fff;
    transition: all 0.2s ease;
  }
  a:hover {
    color: $fontcolor;
  }
}
