@import '../../global.scss';
@import '../../includeMedia/_include-media.scss';

.hero {
    height: 100vh;
    width: 100vw;
    position: fixed;
    z-index: -100;
    background-image: url("https://www.moments.my/data/uploads/hero2020@2x.jpg");
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    object-fit: contain;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;

    .textContainer{
        overflow: hidden;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;

        h1 {
            text-transform: capitalize;
            font-family: "Playfair Display", serif;
            color: $secondarycolor;
            font-size: 3rem;
            font-style: italic;
            margin-bottom: 1rem;
        }

        p {
           text-transform: uppercase;
           color: $secondarycolor;
           font-size: 0.8rem;
           letter-spacing: 5px;
        }
    }
    .chevron {
        position: relative;
        top: 200px;
        color: $secondarycolor;
        font-size:2.5rem;
        opacity: 0.8;
    }


    @include media("<=phone") {

    }
}