@import '../../global.scss';
@import '../../includeMedia/_include-media.scss';

.intro {
    display: flex;
    justify-content:center;
    align-items: center;
    box-shadow: 0px -17px 15px rgb(0 0 0 / 30%);
    margin-top: 100vh;
    z-index: 10;
    position: relative;
    width: 100vw;
    background-color: $secondarycolor;
    overflow: hidden;
    color: $fontcolor;
    font-family: "Baskerville Display", "serif";
    

    .mainContainer {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 90%;
        margin-top: 3rem;
        margin-bottom: 4rem;
        position: relative;

        .leftContainer {
            flex: 4;
            height: 100vh;
            width: 100%;
            background-image: url('https://images.unsplash.com/photo-1502635385003-ee1e6a1a742d?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=687&q=80');
            background-size: contain;
            background-position: center center;
            background-repeat: no-repeat;
            position: relative;
        }

        .rightContainer {
            flex: 4;
            height: 100vh;
            width: 100%;
            padding-left: 1.8rem;
            padding-top: 2rem;
            overflow: hidden;
            
            
            .textContainer {
                height: inherit;
                width: 100%;
                overflow: hidden;
                
                // Letter O
                .introLetter {
                    position: absolute;
                    font-size: 43vw;
                    z-index: 0;
                    opacity: 0.1;
                    color: $fontcolor;
                    top: -8vw;
                    right: 24vw;
                }

                .introText {
                    overflow: hidden;
                    
                    
                    .bgTitle {
                        margin-top:0;
                        font-size: 8.5vw;
                        line-height: 1;
                        overflow:hidden; 

                        // Arrow fa
                        span {
                            color: $primarycolor;

                        }
                    }
                    .smTitle {
                        font-size: 2.5vw;
                        margin-top: 1rem;
                        text-align: right;
                    }
                }
                .container {
                    position: relative;
                    top: 8vw;
                    display: flex;
                    justify-content: center;
                    align-items:flex-start;
                    overflow: hidden;
    
                    .card {
                        flex: 4;

                        &:first-child {
                            padding-right: 1.5rem;
                        }
                        &:last-child {
                            padding-left: 1.5rem;
                        }
                        
                        a {
                            text-decoration: none;
                            color: $fontcolor;
                            
                            h3{
                                font-size: 1rem;

                                transition: all 0.5s ease;
                                margin-bottom: 1rem;

                                span {
                                vertical-align: middle;
                                font-size: 1.5rem;
                                margin-left: 0.6vw;
                                
                                }
            
                            }
                            
                            p {
                                font-size: 0.75rem;
                                transition: all 0.5s ease;
            
                                }

                            &:hover {
                                    
                                h3, p  {
                                    color: $primarycolor;

                                    }
                                }
                            }
                        }
                    
                }
            }
        }
    }

    @include media ("<bgtablet") {

        .mainContainer {
            display: block;
            width: 100%;
            margin-top: 3rem;
            margin-bottom: 4rem;
            position: relative;

            .leftContainer {
                width: 90%;
                height: 100vw;
                background-size: cover;
                background-position: bottom center;
                margin: auto;
            }

            .rightContainer{
                width: 90%;
                height: 100vw;
                text-align: left;

                .textContainer{
                    position: relative;
                    .introLetter {
                            font-size: 50vw;
                            top: -10vw;
                            left:-1vw;
                        }

                    .introText {
                        .bgTitle {
                            font-size: 14vw;
                            
                            // Arrow fa
                            span {
                                color: $primarycolor;
                            }
                        }
                        .smTitle {
                            font-size: 5vw;
                            margin-top: 1vw;
                            margin-bottom: 6vw;
                            text-align: right;
                        }
                    }

                    .container {
                        .card {
                            a {
                                h3{
                                    font-size: 3.3vw;
                                    margin-bottom: 1rem;

                                    span {
                                        vertical-align: middle;
                                        font-size: 2rem;
                                        margin-left: 0.6vw;
                                    }
                                }
                                
                                p {
                                    font-size: 1rem;
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    @include media ('<=phone') {
        .mainContainer {
            
            .rightContainer {
                height: 100vw;
                .textContainer{

                    .container {
                        display: block;
                        .card {
                            text-align: center;
                        }
                        .card:last-child {
                            margin-top: 2rem;
                            margin-bottom: 1rem;
                            padding: 0;
                            
                        }
                    }
                }
            }
        }
    }

}