// Mobile first approach
@import "../../global.scss";
@import "../../includeMedia/_include-media.scss";

.contact {
  width: 100%;
  background-color: #f6f6f6;
  font-family: "Lato", sans-serif;

  .leftContainer {
    padding: 1rem 1rem;
    font-size: 0.8rem;
    font-weight: 300;
    .textContainer {
      h1 {
        font-family: "Playfair Display", sans-serif;
        color: $primarycolor;
        margin-bottom: 1rem;
        font-size: 1.8rem;
      }
      p {
        color: $fontcolor;
        line-height: 1.1rem;
      }

      p:last-child {
        margin-top: 1.56rem;
      }
      .address {
        margin-top: 1.56rem;
        line-height: 1.1rem;

        strong {
          font-weight: 900;
        }

        a {
          text-decoration: none;
          color: $primarycolor;
        }
      }
    }
  }

  .rightContainer {
    padding: 1rem 1rem;

    .formContainer {
      display: flex;
      justify-content: flex;
      flex-direction: column;
      input,
      textarea,
      .button button {
        padding: 0.8rem 1rem;
        font-size: 0.9rem;
        font-weight: 300;
        border-radius: 0;
        border: 1px solid $secondarycolor;
        box-sizing: content-box;
        margin-bottom: 0.8rem;
        font-family: "Lato", sans-serif;
        transition: all 0.5s ease;
      }

      textarea {
        height: 5rem;
      }
      .button button {
        border: 1px solid $primarycolor;
        background-color: $primarycolor;
        color: $fontcolor;
        margin-bottom: 1rem;
        width: 100%;
        cursor: pointer;
        transition: all 0.5s ease;
      }
      .button button:hover {
        background-color: $fontcolor;
        border: 1px solid $fontcolor;
        color: $secondarycolor;
      }
      input:focus,
      textarea:focus {
        outline: none;
        border-color: $fontcolor;
      }
    }
  }
}

@include media(">=tablet") {
  .contact {
    display: flex;

    .leftContainer {
      padding: 1rem 0.8rem 0.5rem 5vw;
      flex: 4;
    }
    .rightContainer {
      padding: 1rem 5vw 0.5rem 0.8rem;
      flex: 4;
    }
  }
}
